// src/pages/dashboard.js
import React, { useEffect, useState, useCallback } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';
import {
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  Tooltip,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
} from 'recharts';

/*
  Dashboard component:
  - uses localStorage 'token' (same as your AuthContext)
  - fetches summary + users
  - visualizes users (pie + bar)
  - table with search, role filter, pagination, delete
*/

/* --------- Axios helper: axios instance with token applied --------- */
const api = axios.create({
  baseURL: process.env.REACT_APP_API_BASE || '', // set if you use a backend host, else relative paths
  timeout: 15000,
});

const applyAuth = () => {
  const token = localStorage.getItem('token');
  if (token) api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  else delete api.defaults.headers.common['Authorization'];
};

/* --------- Colors for charts --------- */
const COLORS = ['#6366F1', '#10B981', '#F97316', '#EF4444', '#64748B'];

/* --------- Utility: format date nicely --------- */
const formatDate = (iso) => {
  try {
    return new Date(iso).toLocaleDateString();
  } catch {
    return iso;
  }
};

const Dashboard = () => {
  const navigate = useNavigate();

  // Summary & visualization state
  const [summary, setSummary] = useState({
    totalUsers: 0,
    activeUsers: 0,
    admins: 0,
    newUsersLast6Months: [], // {month: 'Jan', count: 12}
  });
  const [pieData, setPieData] = useState([]);
  const [barData, setBarData] = useState([]);

  // Users table state
  const [users, setUsers] = useState([]);
  const [loadingUsers, setLoadingUsers] = useState(true);
  const [fetchingSummary, setFetchingSummary] = useState(true);
  const [error, setError] = useState(null);

  // Pagination + filters
  const [page, setPage] = useState(1);
  const limit = 10;
  const [pagination, setPagination] = useState({
    currentPage: 1,
    totalPages: 1,
    totalUsers: 0,
  });
  const [search, setSearch] = useState('');
  const [role, setRole] = useState('');

  /* --------------------- Fetch summary (top cards + charts) --------------------- */
  const fetchSummary = useCallback(async () => {
    setFetchingSummary(true);
    setError(null);
    applyAuth();

    try {
      // If your backend exposes a single endpoint for dashboard stats, use it.
      // Example: GET /api/admin/dashboard
      const res = await api.get('/api/admin/dashboard'); 
      // Expected shape example (backend should return):
      // {
      //   totalUsers: 1200,
      //   activeUsers: 900,
      //   admins: 4,
      //   newUsersLast6Months: [{ month: 'May', count: 20 }, ...]
      // }

      const data = res.data || {};

      setSummary({
        totalUsers: data.totalUsers ?? 0,
        activeUsers: data.activeUsers ?? 0,
        admins: data.admins ?? 0,
        newUsersLast6Months: data.newUsersLast6Months ?? [],
      });

      // Pie: Users / Active / Admins (counts)
      setPieData([
        { name: 'Active', value: data.activeUsers ?? 0 },
        { name: 'Inactive', value: (data.totalUsers ?? 0) - (data.activeUsers ?? 0) },
        { name: 'Admins', value: data.admins ?? 0 },
      ]);

      // Bar: month-wise signups (expects 6 items)
      setBarData(data.newUsersLast6Months ?? []);
    } catch (err) {
      console.error('fetchSummary error', err);
      const status = err.response?.status;
      const msg = err.response?.data?.message || 'Failed to load dashboard summary';
      setError(msg);
      toast.error(msg);
      if (status === 401) {
        // token invalid/expired  force logout / redirect to login
        localStorage.removeItem('token');
        navigate('/login', { replace: true });
      }
    } finally {
      setFetchingSummary(false);
    }
  }, [navigate]);

  /* --------------------- Fetch users (table) --------------------- */
  const fetchUsers = useCallback(async () => {
    setLoadingUsers(true);
    setError(null);
    applyAuth();

    try {
      const params = new URLSearchParams({
        page,
        limit,
      });
      if (search) params.append('search', search);
      if (role) params.append('role', role);

      const res = await api.get(`/api/users?${params.toString()}`);
      // expected response shape:
      // { users: [...], pagination: { currentPage, totalPages, totalUsers } }

      const data = res.data || {};
      setUsers(data.users ?? []);
      setPagination(data.pagination ?? { currentPage: 1, totalPages: 1, totalUsers: 0 });
    } catch (err) {
      console.error('fetchUsers error', err);
      const status = err.response?.status;
      const msg = err.response?.data?.message || 'Failed to load users';
      setError(msg);
      if (status === 401) {
        localStorage.removeItem('token');
        navigate('/login', { replace: true });
      } else {
        toast.error(msg);
      }
    } finally {
      setLoadingUsers(false);
    }
  }, [page, search, role, navigate]);

  useEffect(() => {
    fetchSummary();
    // intentionally not depending on fetchUsers here; both called separately
  }, [fetchSummary]);

  useEffect(() => {
    fetchUsers();
  }, [fetchUsers]);

  /* --------------------- Delete user handler --------------------- */
  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure you want to delete this user? This cannot be undone.')) return;
    applyAuth();
    try {
      const res = await api.delete(`/api/users/${userId}`);
      toast.success(res.data?.message || 'User deleted');
      // refresh table
      fetchUsers();
      // optionally refresh summary too
      fetchSummary();
    } catch (err) {
      console.error('delete user error', err);
      const msg = err.response?.data?.message || 'Failed to delete user';
      toast.error(msg);
      if (err.response?.status === 401) {
        localStorage.removeItem('token');
        navigate('/login', { replace: true });
      }
    }
  };

  /* --------------------- Pagination handlers --------------------- */
  const handleNextPage = () => {
    if (page < pagination.totalPages) setPage((p) => p + 1);
  };
  const handlePrevPage = () => {
    if (page > 1) setPage((p) => p - 1);
  };

  /* --------------------- Derived counts for UI that rely on table data if summary not present --------------------- */
  const displayedTotalUsers = summary.totalUsers || pagination.totalUsers || users.length;
  const displayedActiveUsers = summary.activeUsers || users.filter(u => u.isActive).length;
  const displayedAdmins = summary.admins || users.filter(u => u.role === 'admin').length;

  /* --------------------- Render --------------------- */
  return (
    <div className="p-6 md:p-10 bg-gray-50 min-h-screen">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
            <p className="text-sm text-gray-500 mt-1">Overview of users and activity.</p>
          </div>
          <div>
            <button
              onClick={() => {
                localStorage.removeItem('token');
                navigate('/login', { replace: true });
              }}
              className="px-4 py-2 bg-red-500 hover:bg-red-600 text-white rounded-md"
            >
              Logout
            </button>
          </div>
        </div>

        {/* Summary Cards */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <div className="bg-white p-5 rounded-2xl shadow">
            <p className="text-sm text-gray-500">Total Users</p>
            <p className="text-2xl font-semibold text-gray-900">{displayedTotalUsers}</p>
          </div>

          <div className="bg-white p-5 rounded-2xl shadow">
            <p className="text-sm text-gray-500">Active Users</p>
            <p className="text-2xl font-semibold text-green-600">{displayedActiveUsers}</p>
          </div>

          <div className="bg-white p-5 rounded-2xl shadow">
            <p className="text-sm text-gray-500">Admins</p>
            <p className="text-2xl font-semibold text-indigo-600">{displayedAdmins}</p>
          </div>

          <div className="bg-white p-5 rounded-2xl shadow">
            <p className="text-sm text-gray-500">This Page</p>
            <p className="text-2xl font-semibold text-gray-900">{users.length} users</p>
            <p className="text-xs text-gray-400 mt-1">Showing page {pagination.currentPage} of {pagination.totalPages}</p>
          </div>
        </div>

        {/* Charts Row */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
          {/* Pie Chart */}
          <div className="bg-white p-6 rounded-2xl shadow">
            <h3 className="text-lg font-medium mb-4">User Distribution</h3>
            {fetchingSummary ? (
              <div className="text-center text-gray-500 py-20">Loading chart</div>
            ) : (
              <div style={{ width: '100%', height: 240 }}>
                <ResponsiveContainer>
                  <PieChart>
                    <Pie
                      data={pieData}
                      dataKey="value"
                      nameKey="name"
                      cx="50%"
                      cy="50%"
                      outerRadius={80}
                      label
                    >
                      {pieData.map((entry, idx) => (
                        <Cell key={`cell-${idx}`} fill={COLORS[idx % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              </div>
            )}
          </div>

          {/* Bar Chart */}
          <div className="bg-white p-6 rounded-2xl shadow lg:col-span-2">
            <h3 className="text-lg font-medium mb-4">Signups  Last 6 months</h3>
            {fetchingSummary ? (
              <div className="text-center text-gray-500 py-16">Loading chart</div>
            ) : (
              <div style={{ width: '100%', height: 260 }}>
                <ResponsiveContainer>
                  <BarChart data={barData}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Bar dataKey="count" >
                      {barData.map((entry, idx) => (
                        <Cell key={`bar-${idx}`} fill={COLORS[idx % COLORS.length]} />
                      ))}
                    </Bar>
                  </BarChart>
                </ResponsiveContainer>
              </div>
            )}
          </div>
        </div>

        {/* Filters + Table */}
        <div className="bg-white p-6 rounded-2xl shadow">
          {/* Filters */}
          <div className="flex flex-col md:flex-row gap-4 mb-4">
            <input
              value={search}
              onChange={(e) => { setSearch(e.target.value); setPage(1); }}
              placeholder="Search by name or email..."
              className="flex-grow px-4 py-2 border border-gray-200 rounded-md focus:outline-none"
            />
            <select
              value={role}
              onChange={(e) => { setRole(e.target.value); setPage(1); }}
              className="px-4 py-2 border border-gray-200 rounded-md"
            >
              <option value="">All Roles</option>
              <option value="user">User</option>
              <option value="admin">Admin</option>
            </select>
            <button
              onClick={() => { setSearch(''); setRole(''); setPage(1); }}
              className="px-4 py-2 bg-gray-100 rounded-md"
            >
              Reset
            </button>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Name</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Email</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Role</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Joined</th>
                  <th className="px-6 py-3" />
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {loadingUsers ? (
                  <tr>
                    <td colSpan="6" className="px-6 py-12 text-center text-gray-500">Loading users</td>
                  </tr>
                ) : users.length === 0 ? (
                  <tr>
                    <td colSpan="6" className="px-6 py-12 text-center text-gray-500">No users found.</td>
                  </tr>
                ) : (
                  users.map((user) => (
                    <tr key={user._id}>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-900">{user.firstName} {user.lastName}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                        {user.email}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                          user.role === 'admin' ? 'bg-indigo-100 text-indigo-800' : 'bg-green-100 text-green-800'
                        }`}>
                          {user.role}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                          user.isActive ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                        }`}>
                          {user.isActive ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{formatDate(user.createdAt)}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                        <Link to={`/admin/user/edit/${user._id}`} className="text-indigo-600 hover:text-indigo-900 mr-4">Edit</Link>
                        <button onClick={() => handleDeleteUser(user._id)} className="text-red-600 hover:text-red-900">Delete</button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination Controls */}
          {!loadingUsers && pagination.totalPages > 1 && (
            <div className="mt-6 flex items-center justify-between">
              <div className="flex gap-2">
                <button onClick={handlePrevPage} disabled={page === 1} className="px-3 py-2 bg-white border rounded disabled:opacity-50">Previous</button>
                <button onClick={handleNextPage} disabled={page === pagination.totalPages} className="px-3 py-2 bg-white border rounded disabled:opacity-50">Next</button>
              </div>
              <div className="text-sm text-gray-600">
                Showing <span className="font-medium">{(page - 1) * limit + 1}</span> to <span className="font-medium">{Math.min(page * limit, pagination.totalUsers)}</span> of <span className="font-medium">{pagination.totalUsers}</span> results
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
